const $siteList = $('.siteList')
const $lastLi = $siteList.find('li.lastLi')
const x = localStorage.getItem('x')
const xObject = JSON.parse(x)
const hashMap = xObject || [
  { logo: 'A', url: 'https://www.acfun.cn' },
  {
    logo: 'B',
    url: 'https://www.bilibili.com',
  },
]
const simplifyUrl = (url) => {
  return url
    .replace('https://', '')
    .replace('http://', '')
    .replace('www.', '')
    .replace(/\/.*/, '')
} // ç®€åŒ–url

const render = () => {
  $siteList.find('li:not(.lastLi)').remove()
  hashMap.forEach((node, index) => { // æ·»åŠ indexï¼Œæ–¹ä¾¿åˆ é™¤
    const $li = $(`<li>
      <div class="site">
        <div class="logo">${simplifyUrl(node.url)[0]}</div>
        <div class="link">${simplifyUrl(node.url)}</div>
        <div class="close">
          <svg class="icon">
            <use xlink:href="#icon-close"></use>
          </svg>
        </div>
      </div>
    </li>`).insertBefore($lastLi)
    $li.on('click', () => {
      window.open(node.url)
    }) // æ›¿ä»£aæ ‡ç­¾è·³è½¬
    $li.on('click', '.close', (e) => {
      e.stopPropagation() // é˜»æ­¢å†’æ³¡
      hashMap.splice(index, 1)
      render()
    })
  })
}
render()

$('.addButton').on('click', () => {
  let url = window.prompt('è¯·é—®ä½ è¦æ·»åŠ çš„ç½‘å€æ˜¯å•¥ï¼Ÿ')
  if (url.indexOf('http') !== 0) {
    url = 'https://' + url
  }
  console.log(url)
  hashMap.push({
    logo: url[0],
    url: url,
  })
  render()
})

window.onbeforeunload = () => {
  const string = JSON.stringify(hashMap)
  localStorage.setItem('x', string) // åœ¨æœ¬åœ°çš„localStorageå­˜å‚¨ä¸€ä¸ªxï¼Œå®ƒçš„å€¼ä¸ºstring
}
